.button {
  background: #62aff8;
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  margin-right: 1rem;
  margin-bottom: 2rem;
  color: mediumblue;
}

.button:hover {
  opacity: 0.8;
}

.button.active {
  background: #3e6ae1;
  color: #fff;
}